@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;500;700;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  font-family: "Montserrat";
  text-align: center;
  background-color: #00BFFF;
}

#instructions div p {
  margin-top: 0.4em;
  margin-bottom: 0.4em;

}

#instructions div:last-of-type p{
  margin-top: 1em;
  font-weight: 900;

}

.scoreLine {
  display: flex;
  height: 1.5em;
  font-size: 0.8em;
}

.learning p {
  font-size: 0.9em;

}
#initialEndScore {
  margin: 0;
  margin-bottom: 0.5em;
}
.scoreSectionHead {
  margin: 0;
  text-transform: uppercase;
  font-size: 0.8em;
  font-weight: 900;
}
.finalEndScore {
  margin: 0;
  margin-top: 0.5em;
  font-weight: 900;

}
.scoreBug {
  height: 100%;
  width: 2em;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}

.scoreText {
  margin: auto;
}

.scoreEndS {
  margin: auto;
}

#volumecontrol {
  position: absolute;
  top: 0;
  right: 0;
  color: white;
  z-index: 10;
}



.startButton {
  width: 60%;
  font-size: 2em;
  font-weight: 700;
  border: 5px black solid;
  background-color: #4FFFB0;
  display: flex;
  height: 1.5em;
  margin-top: 0.2em;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.endButton {
  font-size: 1.5em;
}

.startButton:hover {
  background-color: white;

}


.App {
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
  justify-content: center;
}

.rotate, .fire {
  -webkit-user-select: none;
  -ms-user-select: none;
      user-select: none;
  background-repeat: no-repeat;
  background-size: 69%;
  background-position: center;
}

h1 {
  font-weight: 900;
  font-size: 1em;
  color: #4FFFB0;
  z-index: 10;
}


.title {
  color: #4FFFB0;
  font-size: 3em;
  width: 90%;
  margin-top: 0.1em;
  margin-bottom: 0.1em;
}

.spacer {
  height: 0em;
  margin: 0;
}

.intro {
  margin-top: 0.25em;
  margin-bottom: 0.25em;
  font-size: 0.8em;
}

#logo {
  height: 4em;
}
#scorebar {
  position: absolute;
  top: 30px;
  font-weight: 700;
  width: 98%;
  display: flex;
  flex-direction: row;
  text-align: center;
  align-items: center;
  justify-content: space-between;
  color: white;
  padding-left: 1%;
  padding-right: 1%;
}

.container {
  width: 100%;
  display: flex;
  position: relative;
  flex-direction: column;
  text-align: center;
  align-items: center;
  justify-content: flex-start;
}

#buttons {
  position: relative;
  width: 100%;
}


#rbuttons {
  position: absolute;
  left: 0;
  display: flex;
  flex-direction: row;
}

#fbuttons {
  position: absolute;
  right: 0;
  display: flex;
  flex-direction: row;
}

.gbutton {
  height: 25vw;
  width: 20vw;
}
.gbutton button {
  width: 100%;
  height: 80%;
  position: relative;
  border: 2px black solid;
  -webkit-user-select: none;
  -ms-user-select: none;
      user-select: none;
  outline: none;

}


.letter {
  display: none;
}






.innerBorder {
 
  border: 10px darkslategrey solid;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 11;
}
.evenInnerBorder {
  box-sizing: border-box;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 10;

  border: 11px black solid;


}
#yellowBorder{
  border-color: yellow;

}

#redBorder{
  border-color: red;

}

#orangeBorder{
  border-color: orange;

}



#matterBox {
  width: 100%;
  -webkit-user-select: none;
  -ms-user-select: none;
      user-select: none;

}

#matterBox canvas {
  width: 100%;
}

#instructions {
  font-size: 0.8em;
}

@media screen and (min-height:800px) {
  #instructions {
    font-size: 0.9em;
  }
}
@media screen and (min-width: 500px) {
  .container {
    width: 500px;
  }

  #instructions {
    font-size: 0.9em;
  }

  .gbutton {
    height: 100px;
    width: 80px;
  }

  .letter {
    display: block;
  }
  
}
